
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

import {mapState,mapGetters,mapMutations} from 'vuex';

export default {
name: 'app',
components: {
Tab,
TabItem,
XHeader,
Actionsheet
},
created () {
var name1=window.localStorage.getItem('loginName');
var name2=window.localStorage.getItem('loginStatus');
if(name2){
this.mutationIsLogin();
this.mutationIsLoginName(name1);
this.mutationAccessToken(
window.localStorage.getItem('accessToken')
)
}
},
computed: {

...mapGetters(['moreMenus']),
...mapState(['topics','isLogined','unLoginedMoreMenus','LoginedMoreMenus']),
isShowMore:{
get:function(){
return this.$store.state.isShowMore
},
set:function(nval){
this.$store.commit('mutationIsShowMore', nval)
}
},

},
data:function(){
return{

}
},
methods: {
...mapMutations(['mutationIsLogin','mutationIsLoginName','mutationAccessToken']),
routeto:function(index){


this.$router.replace("/topiclist/"+this.topics[index].name)
},
showMoreMenus:function(){//xheader右侧点击显示事件
this.isShowMore=true;
},
onclickMenu:function(menuKey,menuItem){
this.$router.push('/'+menuKey);
}
}
}



