import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
Vue.use(Vuex);
Vue.prototype.$http = axios.create({
    baseURL: 'https://cnodejs.org/api/v1'
})
const store = new Vuex.Store({
    state: {
        topics: [
            { name: 'all', desc: '全部' },
            { name: 'good', desc: '精华' },
            { name: 'share', desc: '分享' },
            { name: 'ask', desc: '问答' },
            { name: 'job', desc: '招聘' },
        ],
        isLogined: false, //默认是未登录
        isShowMore: false, //actionsheet默认不显示
        loginName: '', //登录名
        accessToken: '',
        unLoginedMoreMenus: {
            login: '登录',
        },
        LoginedMoreMenus: {
            center: '个人中心',
            msg: '消息通知',
            logout: '退出登录'
        }
    },
    getters: {
        moreMenus: function(state) {
            if (state.isLogined) {
                return state.LoginedMoreMenus;
            }
            return state.unLoginedMoreMenus;
        }
    },
    mutations: {
        mutationIsShowMore: function(state, payload) {
            // state表示当前store对象的state数据，payload表示函数调用时传入的值
            state.isShowMore = payload
        },
        mutationIsLogin(state) {
            state.isLogined = true
        },
        mutationIsLoginName(state, payload) {
            state.loginName = payload
        },
        mutationAccessToken(state, payload) {
            state.accessToken = payload
        },
        mutationLogout(state) {
            state.isLogined = false;
            state.loginName = '';
            state.accessToken = '';
        }
    },
    actions: {}
})
export default store;


