
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import {mapState,mapGetters} from 'vuex';
export default {
name:'topiclist',
props: ['name'],
components: {
Cell,Group,Scroller,Tab,TabItem
},
computed: {
...mapGetters(['moreMenus']),
...mapState(['topics'])
},
methods:{
routeto:function(index){


this.$router.replace("/topiclist/"+this.topics[index].name)
},
loadMoreData(){
var app=this;
this.page=this.page+1;
this.$http.get('/topics',{
params:{
tab:this.name,
page:this.page,
limit:this.limit
}
}).then(function(res){
for(var i=0;i<res.data.data.length;i++){
app.lists.push(res.data.data[i]);
}

app.$refs.pullup.donePullup();
})
},
freshData(){
var app=this;
this.$http.get('topics',{
params:{
tab:this.name,
page:1,
limit:this.limit
}
}).then(function(res){
app.lists=res.data.data;

app.$refs.pullup.donePulldown();
})
}
},
data:function(){
return{
lists:[],
page:1,//当前页数
limit:15,//每页显示数据
pullupConfig:{//上拉加载配置信息
content:'上拉加载更多',
dowmContent:'松开进行加载',
upContent:'上拉加载更多',
loadingContent:'加载中...'
},
pulldownConfig:{//下拉加载配置信息
content:'下拉加载更多',
dowmContent:'松开进行加载',
upContent:'下拉加载更多',
loadingContent:'加载中...'
}
}
},



beforeRouteEnter:function(to, from, next){
next(function(vm){

vm.$http.get('/topics',{params:{
tab:to.params.name,
page:vm.page,
limit:vm.limit
}}).then(function(res){
console.log(res.data.data);
if(res.data.success)
vm.lists=res.data.data
})
})
},
beforeRouteUpdate:function(to, from, next){

var app=this;
this.$http.get('/topics',{params:{
tab:to.params.name,
page:this.page,
limit:this.limit
}}).then(function(res){
console.log(res);
if(res.data.success){
app.lists=res.data.data
}
})
}
}



